.App {
  text-align: center;
}

* {
  font-family: sans-serif;
}

body {
  background-image: url(https://github.blog/wp-content/uploads/2021/02/card.png?resize=1200%2C630);
  background-repeat: no-repeat;
  background-size: cover;
}

#followers {
  background: rgba(255, 255, 255, 0.74);
  display: flex;
  flex-direction: column;
  width: 40vw;
  margin: 2% auto;
  height: 55vh;
  overflow-y: scroll;
  padding: 1%;
  border-radius: 10px;
  border: 1px solid rgb(100, 100, 100);

}

#followers::-webkit-scrollbar {
  display: none;
}

#user-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 10px 0;
}

#avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

#namecontainer {
  display: flex;
  flex-direction: column;
}

#user-info p:nth-child(2),
#name {
  margin: 0 3%;
  width: 100%;
  text-align: left;
}

#user-info p:nth-child(2) {
  font-size: 0.8rem;
}

#name {
  font-weight: bold;
}

#username {
  height: 2rem;
  width: 30%;
  border: 0;
  border-radius: 5px;
  margin: 0 1%;
  padding: 0.1rem 1%;
  outline: 0;
}

.submitname {
  height: 2.2rem;
  width: 10%;
  border: 0;
  border-radius: 5px;
  background-color: #ff4141;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
}

#title {
  color: white;
}

a {
  text-decoration: none;
  color: inherit;
}

.next {
  background: #ff4141;
  color: white;
  cursor: pointer;
  width: 20%;
  height: 2rem;
  border: 0;
  outline: 0;
  border-radius: 5px;
  margin: 0;
}

#note {
  text-align: left;
  font-size: 1rem;
  padding-left: 1rem;
}

#note li {
  margin: 3%;
}

@media only screen and (max-width: 600px) {
  body {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  #followers {
    width: 80vw;
    margin: 10% auto;
    height: 64vh;
  }

  #username {
    width: 62%;
    padding-left: 3%;
  }

  #name {
    font-size: 0.9rem;
  }

  #user-info p:nth-child(2) {
    font-size: 0.8rem;
  }

  .submitname {
    width: 25%;
  }

  .next {
    width: 35%;
    font-size: 1rem;
  }
}